.about__section {
    height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 1rem;
}

.about__section-name {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: 20px;
}
.about__section-namePart {

}
.about__section-letter {
    font-size: 100px;
    line-height: 50px;
    font-weight: 700;
    letter-spacing: -15px;
    mix-blend-mode: color-burn;
    position: relative;
    -webkit-transition: top ease 0.1s;
    -o-transition: top ease 0.1s;
    transition: top ease 0.1s;
    -webkit-transition-delay: 1.5s;
         -o-transition-delay: 1.5s;
            transition-delay: 1.5s;
    opacity: .9;
}

#s {color: cadetblue}
#t {color: aquamarine}
.e {color: cornflowerblue}
.v {color: darkturquoise}
.e2 {color: steelblue}
.n {color: darkcyan}
.r {color: dodgerblue}
.y {color: lightblue}
.a {color: lightseagreen}
.n2 {color:skyblue}

@-webkit-keyframes moveLetter {
    0% {
        top: 0px;
    }
    50% {
        top: 20px;
    }
    100% {
        top: 0px;
    }
}

@keyframes moveLetter {
    0% {
        top: 0px;
    }
    50% {
        top: 20px;
    }
    100% {
        top: 0px;
    }
}

.hovered {
    -webkit-animation: moveLetter 3s;
            animation: moveLetter 3s;
}

.about__section-text {

    width: 60%;
    padding: 0 1rem;
}

.about__section-img {
    width: 30%;
    min-width: 400px;
}

.about__section-img img {
    width: 100%;
}


